import React, { useEffect, useState } from 'react'
import {prevPage, nextPage} from '../../store/actionCenter'
import {connect} from 'react-redux'

const Navigation = ({page}) => {
    const [maxPage, setMaxPage] = useState('...')

    useEffect(() => {
        (async () => {
            await fetch('/stock/maxpage').then(response => response.json().then(data => {
                setMaxPage(Math.ceil(data.max / 9))
            })).catch(() => {
                setMaxPage('?')
            })
        })()
    }, [])

    return (
        <div id="navigation-bar">
            <span className='nav-btn' onClick={() => {prevPage()}}>{'<'}</span>
            {
                page <= 3 ?
                    <>
                        <a href='#'>1</a>
                        <a href='#'>2</a>
                        <a href='#'>3</a>
                        <a href='#'>...</a>
                        <a href='#'>{maxPage}</a>
                    </> :
                    <>
                        <a href='#'>1</a>
                        <a href='#'>...</a>
                        <a href='#'>{page}</a>
                        <a href='#'>...</a>
                        <a href='#'>{maxPage}</a>
                    </>
            }
            <span className='nav-btn' onClick={() => {nextPage()}}>{'>'}</span>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        page: state.stock.page
    }
}

export default connect(mapStateToProps)(Navigation)