import React, { useEffect, useState } from 'react'
import Card from './Card'
import { connect } from 'react-redux'

import { updateStock } from '../../store/actionCenter'

const Screen = ({ items, page }) => {
    useEffect(() => {
        (async () => {
            await updateStock(page)
        })()
    }, [page])

    return (
        <>
            {
                items.length === 0 ? <>Loading</> :
                    items.map((item) => <Card key={item._id} name={item.name} amount={item.amount} price={item.price} url={item.url} />)
            }
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        items: state.stock.items,
        page: state.stock.page
    }
}

export default connect(mapStateToProps)(Screen)