import store from './store'

const {dispatch} = store

const updateStock = async (page) => {
    await fetch(`/stock/process?page=${page}`).then(response => response.json().then(data => {
        dispatch({type:'updateStock', data})
    }))
}

const prevPage = () => {dispatch({type: 'prev'})}
const nextPage = () => {dispatch({type: 'next'})}

export {
    updateStock,
    prevPage,
    nextPage
}