const stock = (state = {items: [], page: 1}, action) => {
    switch(action.type) {
        case 'updateStock': {
            return {
                ...state,
                items: action.data
            }
        }

        case 'prev': {
            return {
                ...state,
                page: state.page - 1
            }
        }

        case 'next': {
            return {
                ...state,
                page: state.page + 1
            }
        }
        default: return state
    }
}

export default stock