import React from 'react'
import cookie from 'cookie'

const checkUserLogin = () => {
    const cookies = cookie.parse(document.cookie || '')
    if (cookies.token) return true
    return false
}

const addItem = (name) => {
    if (!sessionStorage.getItem('cart')) return sessionStorage.setItem('cart', JSON.stringify({cart: [name]}))
    const cart = JSON.parse(sessionStorage.getItem('cart'))
    cart.cart.push(name)
    sessionStorage.setItem('cart', JSON.stringify(cart))
}

const Card = ({ name, amount, price, url }) => {
    var disableAddBtn = true
    if (checkUserLogin()) disableAddBtn = false
    return (
        <div className='card'>
            <a href='#'><img src={url} /></a>
            <p>{name}</p>
            <p>{amount}</p>
            <p>{price}</p>
            <button disabled={disableAddBtn} onClick={() => {addItem(name)}}>ADD</button>
            <a href={`/item/${name}`}>INFO</a>
        </div>
    )
}

export default Card